* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html, body {
  height: 100%;
}

body {
  background-color: #f6f8fc;
  font-family: Roboto, sans-serif;
}

.chip {
  filter: brightness(1.2);
  text-align: center;
  border-radius: 1rem;
  padding: .25rem .5rem;
}

.pokemon-types {
  flex-direction: column;
  gap: .5rem;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.pokemon-types--horizontal {
  flex-direction: row;
}

.normal {
  background-color: #a6a877;
}

.grass {
  background-color: #49d0b0;
}

.fire {
  background-color: #fd6c6d;
}

.water {
  background-color: #a4d9ff;
}

.electric {
  background-color: #cbd2e0;
}

.ice {
  background-color: #98d5d7;
}

.ground {
  background-color: #dfbf69;
}

.flying {
  background-color: #a98ff0;
}

.poison {
  background-color: #a040a0;
}

.fighting {
  background-color: #bf3029;
}

.psychic {
  background-color: #f65687;
}

.dark {
  background-color: #725847;
}

.rock {
  background-color: #b8a137;
}

.bug {
  background-color: #a8b720;
}

.ghost {
  background-color: #6e5896;
}

.steel {
  background-color: #b9b7cf;
}

.dragon {
  background-color: #6f38f6;
}

.fairy {
  background-color: #f9aec7;
}

.stats-table {
  margin: 1rem .5rem;
  font-size: .75rem;
}

.stats-table__stat {
  justify-content: stretch;
  gap: .5rem;
  margin: .75rem 0;
  display: flex;
}

.stats-table__stat > :last-child {
  flex: 1;
}

.stats-table__stat > * {
  min-width: 1.5rem;
  text-transform: capitalize;
}

.stats-table__title {
  color: #bbb;
  width: 6.25rem;
}

.stats-table meter::-webkit-meter-bar {
  height: 3px;
  border: none;
}

body {
  color: #fff;
}

main {
  min-height: 100%;
  flex-direction: column;
  display: flex;
  overflow: hidden;
}

.header {
  justify-content: space-between;
  padding: 2rem 1rem;
  display: flex;
}

main, .header {
  max-width: 992px;
  margin: auto;
}

.back-button {
  color: inherit;
  text-decoration: none;
}

.pokemon-header {
  justify-content: space-between;
  align-items: center;
  padding: 0 1rem;
  display: flex;
}

.pokemon-header__title {
  text-transform: capitalize;
  margin: 0;
}

.pokemon-header__number {
  font-size: 1.2rem;
  font-weight: bold;
}

.pokemon-display {
  z-index: 0;
  position: relative;
}

.pokemon-display__background-ellipsis {
  z-index: -1;
  margin: auto;
  position: absolute;
  bottom: 8rem;
  left: -11rem;
  right: 0;
}

.pokemon-display__background-pokeball {
  z-index: -1;
  margin: auto;
  position: absolute;
  bottom: -1rem;
  left: 0;
  right: -12rem;
}

.pokemon-display__image {
  max-width: 100%;
  max-height: 18rem;
  filter: drop-shadow(2px 2px 4px #0000005a);
  margin: auto;
  display: block;
  position: relative;
  top: 1.5rem;
}

.pokemon-details {
  color: #333;
  background: #fff;
  border-radius: 2rem 2rem 0 0;
  flex: 1;
  padding: 3rem 1rem;
}

.pokemon-details__tabs {
  justify-content: space-evenly;
  gap: 1rem;
  font-size: .75rem;
  list-style: none;
  display: flex;
}

.pokemon-details__tab {
  color: #bbb;
  padding: 0 .5rem 2rem;
  text-decoration: none;
}

.pokemon-details__page {
  display: none;
}

.pokemon-details__page:target {
  animation: .2s linear fade;
  display: block;
}

@keyframes fade {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}



